@font-face {
  /*logo and titles*/
  font-family: 'AlfaSlabOne-Regular';
  src: local("AlfaSlabOne-Regular"), url(./fonts/AlfaSlabOne-Regular.ttf);
}

@font-face {
  /*home paragraphs*/
  font-family: 'SourceCodePro-Regular';
  src: local("SourceCodePro-Regular"), url(./fonts/SourceCodePro-Regular.ttf);
}

@font-face {
  /*navbar, footer, uppercase subtitles*/
  font-family: 'Staatliches-Regular';
  src: local("Staatliches-Regular"), url(./fonts/Staatliches-Regular.ttf);
}

@font-face {
  /*sections paragraphs, projects info, form input value*/
  font-family: 'Dosis';
  src: local("Dosis"), url(./fonts/Dosis-VariableFont_wght.ttf);
}

@font-face {
  /*freecodecamp subtitle, input placeholder*/
  font-family: 'RobotoSlab-Light';
  src: local("RobotoSlab-Light"), url(./fonts/RobotoSlab-Light.ttf);
}

@keyframes initialHomeLoad {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes oglezFirstLoad {
  from {
    opacity: 0;
    transform: scale(0.2);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeOutLoader {
  0% {
    opacity: 1;
  }
  10% {
    opacity: .9;
  }
  20% {
    opacity: .8;
  }
  30% {
    opacity: .7;
  }
  40% {
    opacity: .6;
  }
  50% {
    opacity: .5;
  }
  60% {
    opacity: .4;
  }
  70% {
    opacity: .3;
  }
  80% {
    opacity: .2;
  }
  90% {
    opacity: .1;
  }
  100% {
    opacity: 0;
  }
}

.navbar {
  background-color: white;
  z-index: 10;
  height: 50px;
}

.navbar .navbar-brand {
  height: 100%;
  width: auto;
  margin-left: 3rem;
  display: flex;
  align-items: center;
}

.navbar .navbar-brand img {
  width: 80%;
}

.navbar #navbarNav {
  margin-right: 2rem;
}

.navbar #navbarNav .navbar-nav .nav-link {
  width: 80px;
  text-align: center;
  margin-left: 30px;
  font-size: 14px;
  font-family: 'Staatliches-Regular';
  letter-spacing: 1px;
  height: 100%;
  color: #855a5a;
  transition: all 1s;
}

.navbar #navbarNav .navbar-nav .nav-link:hover {
  background-color: #f3f3f3;
  transition: all .3s;
}

.navbar #navbarNav .navbar-nav .menu-link-underline {
  width: 100%;
  height: 4px;
  border-radius: 5px;
  background-color: #963e3e;
  transform: scale(0);
  transition: all .3s;
  transition-timing-function: cubic-bezier(0, -0.27, 0.57, -0.25);
}

.navbar #navbarNav .navbar-nav .transform-underline {
  transform: scale(1);
  transition-timing-function: cubic-bezier(0.1, 1.05, 0.21, 1.27);
}

.navbar #navbarNav .navbar-nav .active-link {
  background-color: #ffa1a1;
  transform: scale(1);
}

.load-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #111111;
}

.load-wrapper #loader {
  animation: fadeOutLoader 2.2s;
}

#absolute-wrapper {
  height: auto;
  margin-top: 50px;
  width: 100%;
  position: relative;
}

#absolute-wrapper .animation-wrapper {
  animation: initialHomeLoad .5s;
}

#section-wrapper {
  position: relative;
  top: 0;
  min-height: calc(100vh - 102px);
  width: 95%;
  margin: 0 auto;
  padding: 0;
}

.appear {
  /*logo effect*/
  opacity: 0;
  transform: translateX(-100%);
}

#row-cont-home {
  background-color: black;
  min-height: calc(100vh - 102px);
  width: 100%;
  background-size: cover;
  background-position: center right;
  background-repeat: no-repeat;
}

#row-cont-home .txt-cont {
  margin: auto 0;
  background-color: rgba(201, 181, 181, 0.07);
  color: white;
  padding: 3rem;
  width: 50%;
}

#row-cont-home .txt-cont .logo {
  width: 100%;
  transition: all .4s;
  font-size: 120px;
  font-family: "AlfaSlabOne-Regular", sans-serif;
  letter-spacing: 10px;
  border-bottom: 1px solid #424242;
  margin-bottom: 1rem;
}

#row-cont-home .txt-cont .info-txt {
  width: 70%;
  font-size: 22px;
  font-family: "SourceCodePro-Regular";
  opacity: 1;
  transition: opacity 2s;
}

#row-cont-home .txt-cont .fade-in-txt {
  opacity: 0;
}

#row-cont-home #social-net {
  position: absolute;
  bottom: 3rem;
  left: 3rem;
  text-align: center;
  color: #fff;
}

#row-cont-home #social-net h1 {
  font-size: 18px;
  font-family: 'Staatliches-Regular';
  letter-spacing: 2px;
}

#row-cont-home #social-net .social-icons {
  background-color: rgba(90, 90, 90, 0.494);
  border-top: 1px solid #828282;
}

#row-cont-home #social-net .social-icons .col-icon {
  padding: 5px 0;
}

#row-cont-home #social-net .social-icons .col-icon a {
  color: #d18f8f;
}

#row-cont-home #social-net .social-icons .col-icon a svg {
  font-size: 20px;
}

#row-cont-home #social-net .social-icons .col-icon a:hover {
  color: white;
  transition: all .5s;
}

#row-cont-home .rombo-scroll {
  position: absolute;
  bottom: 0;
  left: calc(50% - 15px);
  width: 0;
  height: 0;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  border-bottom: 30px solid rgba(212, 212, 212, 0.576);
}

/***********projects and contact page*********/
.title-container {
  width: 100%;
  height: 60vh;
  background-color: #25344b;
  color: #fff;
  position: relative;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
}

.title-container .txt-wrapper {
  position: relative;
  text-align: center;
  max-width: 500px;
  top: -20px;
  padding: 1rem;
  background-color: rgba(17, 17, 17, 0.267);
}

.title-container .txt-wrapper .title-section {
  font-size: 55px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-family: 'AlfaSlabOne-Regular';
}

.title-container .txt-wrapper #title-section-text {
  font-size: 20px;
  margin-top: 10px;
  letter-spacing: 1px;
  font-family: 'Dosis', sans-serif;
}

.style-bk-img {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  filter: blur(3px) grayscale(0.3);
  top: 0;
  left: 0;
}

.hidden-sect {
  visibility: hidden;
  opacity: 0;
  transform: translateY(20%);
  transition: all .6s;
}

#section-projects .projects {
  width: 95%;
  position: relative;
  margin: 0 auto;
  top: -100px;
}

#section-projects .projects .row .columna {
  -webkit-box-shadow: -2px -2px 13px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -2px -2px 13px 1px rgba(0, 0, 0, 0.75);
  box-shadow: -2px -2px 13px 1px rgba(0, 0, 0, 0.75);
  height: auto;
  margin: 10px;
  background-color: #fff;
}

#section-projects .projects .row .columna .project-info {
  color: #b48080;
  font-size: 16px;
  font-weight: 500;
}

#section-projects .projects .row .columna .project-info .img {
  height: auto;
  overflow: hidden;
  border: #ffebe2 solid 1px;
}

#section-projects .projects .row .columna .project-info .img img {
  width: 100%;
  transition: .5s;
}

#section-projects .projects .row .columna .project-info .project-web-info {
  padding: 20px;
  font-family: "Dosis";
  letter-spacing: 1px;
  font-weight: 600;
}

#section-projects .projects .row .columna .hover-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  cursor: pointer;
}

#section-projects .projects .row .columna .hover-image {
  transform: scale(1.1);
}

#section-projects .divisor {
  width: 20px;
  height: 2px;
  background-color: #c08b8b;
  margin: 10px auto;
}

#section-projects .school-project-wrapper {
  width: 60%;
  padding-bottom: 50px;
}

#section-projects .school-project-wrapper h4 {
  font-family: 'RobotoSlab-Light';
  font-size: 30px;
  transition: all .5s;
  color: #494949;
}

#section-projects .school-project-wrapper .hidden-subtitle {
  opacity: 0;
  transition: all .7s;
}

#section-projects .school-project-wrapper .row {
  background-color: rgba(214, 214, 214, 0.705);
  padding: 50px 0;
}

#section-projects .school-project-wrapper .row .col-sm-5 {
  margin: 10px;
  padding: 0;
}

#section-projects .school-project-wrapper .row .col-sm-5 .mose-over-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  cursor: pointer;
}

#section-projects .school-project-wrapper .row .col-sm-5 .project-wrapper .image {
  height: auto;
  overflow: hidden;
  border: #ffebe2 solid 2px;
}

#section-projects .school-project-wrapper .row .col-sm-5 .project-wrapper .image img {
  width: 100%;
}

#section-projects .school-project-wrapper .row .col-sm-5 .project-wrapper .proj-info-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #526982;
  border: #65798d solid 1px;
  opacity: 0;
  transition: all .6s;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#section-projects .school-project-wrapper .row .col-sm-5 .project-wrapper .proj-info-wrapper .text-inf {
  width: 70%;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 1px;
  font-family: "Dosis";
  color: #8ac7c6;
}

#section-projects .school-project-wrapper .row .col-sm-5 .project-wrapper .proj-info-wrapper .line {
  background-color: #6d9796;
  width: 80%;
  height: 1px;
}

#section-projects .school-project-wrapper .row .col-sm-5 .project-wrapper .proj-info-hidden {
  opacity: 1;
  transition: opacity .6s;
  box-shadow: -5px 5px 8px 0 rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -5px 5px 8px 0 rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -5px 5px 8px 0 rgba(0, 0, 0, 0.75);
}

/***********projects page*********/
#contact-container {
  background-color: #ffffff;
  -webkit-box-shadow: -2px -2px 13px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -2px -2px 13px 1px rgba(0, 0, 0, 0.75);
  box-shadow: -2px -2px 13px 1px rgba(0, 0, 0, 0.75);
  border-radius: 5px;
  padding: 10px;
  position: relative;
  top: -100px;
  display: flex;
  flex-direction: row;
  max-width: 1100px;
  margin: 0 auto;
}

#contact-container .alertt-message {
  position: absolute;
  z-index: 3;
}

#contact-container form {
  text-align: left;
  position: relative;
  width: 50%;
  min-height: 300px;
  border: 1px dotted #d1cbcb;
  padding: 20px;
}

#contact-container form .form-group {
  margin-bottom: 1rem;
}

#contact-container form .form-group .form-control {
  background-color: #ffffff;
  color: #354583;
  font-family: 'Dosis';
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 16px;
  height: 40px;
  border: 0;
  border-bottom: 1px solid #e6dfdf;
  border-radius: 7px;
}

#contact-container form .form-group .form-control:focus {
  border-bottom: 1px solid #f88a68;
  outline: none;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}

#contact-container form .form-group .form-control::placeholder {
  color: #a7a7a7;
  font-size: 12px;
  font-family: "RobotoSlab-Light";
  letter-spacing: 1px;
  text-transform: uppercase;
}

#contact-container form .form-group textarea {
  width: 100%;
  min-height: 150px;
}

#contact-container form .form-group textarea::placeholder {
  position: relative;
  top: 2px;
}

#contact-container form button {
  border: 0;
  width: 80px;
  height: 40px;
  letter-spacing: 3px;
  font-size: 14px;
  font-family: 'Staatliches-Regular';
  font-weight: 700;
  background-color: #284774;
  transition: all .3s;
  cursor: pointer;
}

#contact-container form button:focus {
  outline: none;
}

#contact-container form button:hover {
  width: 130px;
  background-color: #a75b5b;
  transition: all .5s;
}

#contact-container #right-side-form {
  background-color: #cad2da;
  color: #344f77;
  width: auto;
  margin: auto auto;
  padding: 20px;
  position: relative;
}

#contact-container #right-side-form #right-info {
  font-size: 15px;
  font-weight: bolder;
  font-family: 'RobotoSlab-Light';
}

#contact-container #right-side-form #right-info div {
  margin: 1.5rem 0;
}

#contact-container #right-side-form #right-info svg {
  margin: 0 1rem;
}

#contact-container #right-side-form #right-info #social-on-contact a {
  color: #344f77;
}

#contact-container #right-side-form #right-info #social-on-contact a svg {
  font-size: 20px;
}

#contact-container #right-side-form #right-info #social-on-contact a:hover {
  color: #a75b5b;
}

@keyframes load-footer {
  0% {
    font-size: 0rem;
  }
  10% {
    font-size: .15rem;
  }
  20% {
    font-size: .3rem;
  }
  30% {
    font-size: .45rem;
  }
  40% {
    font-size: .6rem;
  }
  50% {
    font-size: .85rem;
  }
  60% {
    font-size: 1rem;
  }
  70% {
    font-size: 1.15rem;
  }
  80% {
    font-size: 1.3rem;
  }
  90% {
    font-size: 1.45rem;
  }
  100% {
    font-size: 1.5rem;
  }
}

.footer-first-enter {
  transition: all .6s;
  opacity: 0;
}

#footer {
  width: 100%;
  height: 50px;
  font-family: 'Staatliches-Regular';
  letter-spacing: 1px;
}

#footer .foot-cont {
  width: 96%;
  position: absolute;
  left: 2%;
  font-size: 16px;
  font-weight: 500;
  transition: all .6s;
  color: #616161;
}

#footer .foot-cont .name {
  height: 50px;
  display: flex;
  align-items: center;
}

#footer .foot-cont .social-icons-footer {
  font-size: 18px;
  position: absolute;
  right: 0;
  top: 10px;
}

#footer .foot-cont .social-icons-footer a {
  margin: 0 10px;
  color: #616161;
}

#footer .foot-cont .social-icons-footer a:last-of-type {
  margin-right: 0;
}

#footer .foot-cont .social-icons-footer a:hover {
  color: #d18f8f;
  transition: all .3s;
}

@media screen and (max-width: 1200px) {
  #row-cont-home {
    background-position: bottom center;
  }
  #row-cont-home .txt-cont {
    padding: 4rem;
    width: 60%;
  }
  #row-cont-home .txt-cont .logo {
    font-size: 100px;
  }
  #row-cont-home .txt-cont .info-txt {
    font-size: 20px;
  }
  #row-cont-home #social-net {
    position: absolute;
    bottom: 4rem;
    left: 4rem;
  }
  #row-cont-home #social-net h1 {
    font-size: 16px;
  }
  #row-cont-home #social-net .social-icons .col-icon a svg {
    font-size: 17px;
  }
  #row-cont-home .rombo-scroll {
    left: calc(50% - 10px);
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid rgba(212, 212, 212, 0.576);
  }
  .title-container {
    height: 55vh;
  }
  .title-container .txt-wrapper .title-section {
    font-size: 45px;
  }
  .title-container .txt-wrapper #title-section-text {
    font-size: 19px;
  }
  #section-projects .projects {
    width: 90%;
  }
  #section-projects .projects .row .columna .project-info {
    font-size: 14px;
  }
  #section-projects .projects .row .columna .project-info .project-web-info {
    padding: 8px;
  }
  #section-projects .school-project-wrapper h4 {
    font-size: 25px;
  }
  #section-projects .school-project-wrapper .row .col-sm-5 .project-wrapper .proj-info-wrapper .text-inf {
    font-size: 15px;
  }
  #contact-container {
    width: 90%;
  }
}

@media screen and (max-width: 1200px) and (orientation: landscape) {
  #row-cont-home .txt-cont .logo {
    font-size: 80px;
    margin-bottom: 20px;
  }
  #row-cont-home .txt-cont .info-txt {
    width: 90%;
    font-size: 15px;
  }
  .title-container {
    height: 55vh;
  }
}

