  #contact-container #right-side-form #right-info {
    font-size: 13px;
  }
  #contact-container #right-side-form #right-info svg {
    margin: 0 3px;
  }
  #contact-container #right-side-form #right-info #social-on-contact a {
    margin: 0 10px;
  }
  #contact-container #right-side-form #right-info #social-on-contact a svg {
    font-size: 18px;
  }
  #footer .foot-cont {
    font-size: 14px;
  }
  #footer .foot-cont .social-icons-footer {
    font-size: 16px;
  }
}

@media screen and (max-width: 991px) and (orientation: landscape) {
  #row-cont-home {
    background-size: 65%;
    background-position: bottom right;
  }
  #row-cont-home .txt-cont {
    position: absolute;
    top: -20px;
  }
  #row-cont-home .txt-cont .logo {
    font-size: 4rem;
    margin-bottom: 3px;
  }
  #row-cont-home .txt-cont .info-txt {
    width: 100%;
    font-size: 13px;
  }
  #row-cont-home #social-net {
    position: absolute;
    bottom: 20px;
    left: 40px;
  }
  #row-cont-home #social-net h1 {
    font-size: 13px;
  }
  #row-cont-home #social-net .social-icons .col-icon a svg {
    font-size: 14px;
  }
  .title-container {
    height: 75vh;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: calc(5px + (4) * ((100vw - 320px) / (1600 - 300)));
  }
  .navbar .navbar-brand img {
    width: 55%;
  }
  .navbar .navbar-toggler {
    font-size: 12px;
  }
  .navbar #navbarNav .navbar-nav {
    width: 50%;
  }
  .navbar #navbarNav .navbar-nav .nav-link {
    width: 50%;
    font-size: 11px;
  }
  .navbar #navbarNav .navbar-nav .menu-link-underline {
    height: 2px;
  }
  #row-cont-home {
    background-size: 175%;
  }
  #row-cont-home .txt-cont {
    position: absolute;
    top: 50px;
  }
  #row-cont-home .txt-cont .logo {
    font-size: 10rem;
  }
  #row-cont-home .txt-cont .info-txt {
    width: 100%;
    font-size: 15px;
  }
  #row-cont-home #social-net {
