@media screen and (max-width: 767px) and (orientation: landscape) {
  #row-cont-home {
    background-size: 70%;
    background-position: bottom right;
  }
  #row-cont-home .txt-cont {
    position: absolute;
    top: -15px;
  }
  #row-cont-home .txt-cont .logo {
    font-size: 8.5rem;
    margin-bottom: 3px;
  }
  #row-cont-home .txt-cont .info-txt {
    width: 100%;
    font-size: 12px;
  }
  #row-cont-home #social-net {
    position: absolute;
    bottom: 5px;
    left: 10px;
  }
  #row-cont-home #social-net h1 {
    font-size: 11px;
  }
  #row-cont-home #social-net .social-icons .col-icon a svg {
    font-size: 10px;
  }
  .title-container {
    height: 60vh;
  }
}

@media screen and (max-width: 575px) {
  #section-projects .school-project-wrapper {
    width: 65%;
  }
}

@media screen and (max-width: 330px) {
  html {
    font-size: calc(5px + (4) * ((100vw - 320px) / (1600 - 300)));
  }
  #row-cont-home {
    background-size: 200%;
  }
  #row-cont-home .txt-cont .logo {
    font-size: 7.5rem;
  }
  #row-cont-home .txt-cont .info-txt {
    font-size: 13px;
  }
  #row-cont-home #social-net h1 {
    font-size: 12px;
  }
